// src/index.js
import VanillaMasker from 'vanilla-masker';

// Função para aplicar a máscara ao telefone
function aplicarMascaraTelefone(input) {
  VanillaMasker(input).maskPattern('(99) 99999-9999');
}

// Função para formatar o nome, exceto palavras específicas
function formatarNome(nome) {
  const palavrasIgnoradas = ['de', 'da', 'do', 'dos', 'das', 'di'];
  return nome
    .toLowerCase()
    .split(' ')
    .map((palavra) => {
      if (palavrasIgnoradas.includes(palavra)) {
        return palavra; // Mantém em minúsculas
      }
      return palavra.charAt(0).toUpperCase() + palavra.slice(1); // Capitaliza a primeira letra
    })
    .join(' ');
}

// Carregar condomínios ao abrir a página
document.addEventListener('DOMContentLoaded', async () => {
  // Carregar lista de condomínios
  const response = await fetch('/api/apartamentos/condominios');
  const condominios = await response.json();

  const selectCondominio = document.getElementById('nomeCondominio');
  condominios.forEach((condominio) => {
    const option = document.createElement('option');
    option.value = condominio.nome;
    option.textContent = condominio.nome;
    selectCondominio.appendChild(option);
  });

  // Aplicar máscara ao campo de telefone inicial
  const telefoneInputInicial = document.querySelector('input[name="moradorTelefone"]');
  if (telefoneInputInicial) {
    aplicarMascaraTelefone(telefoneInputInicial);
  }

  // Formatar o nome em tempo real no campo inicial
  const nomeInputInicial = document.querySelector('input[name="moradorNome"]');
  if (nomeInputInicial) {
    nomeInputInicial.addEventListener('input', (e) => {
      e.target.value = formatarNome(e.target.value);
    });
  }
});

// Tornar as funções globais
window.adicionarMorador = function() {
  const moradoresDiv = document.getElementById('moradores');
  const novoMorador = document.createElement('div');
  novoMorador.classList.add('morador');
  novoMorador.innerHTML = `
    <input type="text" name="moradorNome" placeholder="Nome do Morador" required>
    <input type="text" name="moradorTelefone" placeholder="Telefone" required>
    <button type="button" onclick="removerMorador(this)">Remover</button>
  `;
  moradoresDiv.appendChild(novoMorador);

  // Aplicar a máscara ao novo campo de telefone
  const telefoneInput = novoMorador.querySelector('input[name="moradorTelefone"]');
  aplicarMascaraTelefone(telefoneInput);

  // Formatar o nome em tempo real
  const nomeInput = novoMorador.querySelector('input[name="moradorNome"]');
  nomeInput.addEventListener('input', (e) => {
    e.target.value = formatarNome(e.target.value);
  });
};

window.removerMorador = function(botao) {
  const moradorDiv = botao.parentElement;
  moradorDiv.remove();
};

function removerMascaraTelefone(telefone) {
  return telefone.replace(/\D/g, ''); // Remove tudo que não for número
}

function validarTelefone(telefone) {
  const telefoneSemMascara = removerMascaraTelefone(telefone);
  return telefoneSemMascara.length === 11; // Valida se o telefone tem 11 dígitos
}

document.getElementById('cadastroForm').addEventListener('submit', async (e) => {
  e.preventDefault();

  const nomeCondominio = document.getElementById('nomeCondominio').value.trim();
  const bloco = document.getElementById('bloco').value.trim();
  const apartamento = document.getElementById('apartamento').value.trim();

  // Validação de campos obrigatórios
  if (!nomeCondominio || !bloco || !apartamento) {
    alert('Preencha todos os campos obrigatórios.');
    return;
  }

  // Validação de tamanho máximo do bloco
  if (bloco.length > 50) {
    alert('O campo "Bloco" deve ter no máximo 50 caracteres.');
    return;
  }

  // Validação de moradores
  const moradores = [];
  const moradoresDivs = document.querySelectorAll('.morador');
  if (moradoresDivs.length === 0) {
    alert('Adicione pelo menos um morador.');
    return;
  }

  let telefoneValido = true;
  moradoresDivs.forEach((morador) => {
    const nome = formatarNome(morador.querySelector('input[name="moradorNome"]').value.trim());
    const telefoneComMascara = morador.querySelector('input[name="moradorTelefone"]').value.trim();
    const telefone = removerMascaraTelefone(telefoneComMascara); // Remove a máscara

    if (!nome || !telefone) {
      alert('Preencha todos os campos dos moradores.');
      telefoneValido = false;
      return;
    }

    if (!validarTelefone(telefoneComMascara)) {
      alert('Telefone inválido. O telefone deve ter 11 dígitos.');
      telefoneValido = false;
      return;
    }

    moradores.push({ nome, telefone });
  });

  if (!telefoneValido) return;

  // Enviar dados para o backend
  const response = await fetch('http://localhost:5000/api/apartamentos/cadastrar', {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({ nomeCondominio, bloco, apartamento, moradores }),
  });

  const result = await response.json();
  alert(result.message);

  // Limpar o formulário após o cadastro
  document.getElementById('cadastroForm').reset();
  document.getElementById('moradores').innerHTML = '';
});